import { Component } from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import * as moment from 'moment';
import {CalendarEvent} from "../../../providers/calendar/calendarEvent.Model";
import {AuthManagerProvider} from "../../../providers/auth-manager/auth-manager";
import {CalendarProvider} from "../../../providers/calendar/calendarProvider";

@IonicPage()
@Component({
  selector: 'page-event-modal',
  templateUrl: 'event-modal.html',
})
export class EventModalPage {

  titlePlaceholderText = "Titel"; // Title
  eventLocationPlaceholderText = "Veranstaltungsort"; // Event location
  notesPlaceholderText = "Notizen"; // Notes
  startDatePlaceholderText = "Start"; // Start
  endDatePlaceholderText = "Ende"; // End
  typeOfEventPlaceholderText = "Art der Veranstaltung"; // Type of event
  patientsInvitedPlaceholderText = "Eingeladene Patienten"; // Patients invited
  allDayPlaceholderText = "Den ganzen Tag?"; // All Day?
  addEventPlaceholderText = "Event hinzufügen"; // Add Event
  eventDetailsPlaceholderText = "Veranstaltungsdetails"; // Event Details

  currentTime = moment().toDate();
  event: CalendarEvent = new CalendarEvent();
  startTime = moment(this.currentTime).format();
  endTime= moment(EventModalPage.createDateAddingHours(this.currentTime,1)).format();
  minDate = moment(this.currentTime).format();
  eventTypeInText: string = CalendarEvent.getEventTypeByNumber(this.event.typeOfMeeting);
  isMedical = this.authManager.isMedical;
  hasShowedMessageForUpdatingPatientsList = false;

  public static createDateAddingHours(date:Date, hours:number): Date {
    let temp:Date = new Date();
    temp.setTime(date.getTime() + (hours*60*60*1000));
    return temp;
  }

  constructor(public navCtrl: NavController, private navParams: NavParams, public viewCtrl: ViewController, public alertController: AlertController, public authManager: AuthManagerProvider, private calendarProvider : CalendarProvider) {
    let selectedDayDate = moment(this.navParams.get('selectedDay')).toDate();
    if(this.currentTime.getDay()!=selectedDayDate.getDay() || this.currentTime.getMonth()!=selectedDayDate.getMonth() || this.currentTime.getFullYear()!=selectedDayDate.getFullYear()) {
      let preselectedDate = moment(this.navParams.get('selectedDay')).format();
      let preselectedDateEnd = moment(EventModalPage.createDateAddingHours(this.navParams.get('selectedDay'),1)).format();
      this.startTime = preselectedDate;
      this.endTime = preselectedDateEnd;
    }
  }

  cancel() {
    this.viewCtrl.dismiss();
  }

  save() {
    this.event.startTime = moment(this.startTime).toDate();
    this.event.endTime = moment(this.endTime).toDate();
    this.viewCtrl.dismiss(this.event);
  }

  ionViewDidLoad() {}

  private parsePatientsList(array) {
      let result = [];
      for (let user of array) {
          if (user.hasOwnProperty("id")) {
              result.push(user["id"]);
          }
      }
      return result;
  }

  private isIncluded(item, array) {
      if(array) {
          for(let obj of array){
              if (obj==item)
                  return true;
          }
      }
      return false;
  }

  public promptToSelectPatients() {
      console.log("Inside the select patients function.");
      if (this.calendarProvider.usersListFinishedUpdate==true && (!this.calendarProvider.usersListUpdating==true)) {
          console.log("Inside the if!!! of the select patients function.");
          let emails = this.parsePatientsList(this.calendarProvider.usersList);
          let alert = this.alertController.create({
              title: "Patienten für den Termin" // "Patients for the appointment"
          });
          let selectedPatients = [];
          if(this.event.participants!="") {
              selectedPatients = this.event.participants.split(",");
          }
          for(let patientEmail of emails) {
              alert.addInput({
                  type: 'checkbox',
                  label: patientEmail,
                  value: patientEmail,
                  checked: this.isIncluded(patientEmail,selectedPatients)
              });
          }
          alert.addButton('Cancel');
          alert.addButton({
              text: 'OK',
              handler: data => {
                  let participants = "";
                  for(let email of data) {
                      participants += email + ",";
                  }
                  participants = participants.substring(0,participants.length-1);
                  console.log("Participants: ", participants);
                  this.event.participants = participants;
              }
          });
          alert.present();
      }
      else {
          console.log("Inside the else :((( !!! of the select patients function.");
          if (this.calendarProvider.usersListUpdating==false && this.calendarProvider.usersListFinishedUpdate == false) {
              console.log("Starting updating the list?");
              this.calendarProvider.updateUserList();
          }
          let alert = this.alertController.create({
              title: "Patientenliste abrufen", //"Retrieving patients' list"
              subTitle: "Bitte warten Sie. Die Anwendung ruft die Patientenliste ab und es erscheint ein neues Fenster zur Auswahl der Patienten." //"Please wait. The application retrieves the patients' list and a new window will appear to select patients."
          });
          alert.addButton({
              text: 'OK'
          });
          if (this.hasShowedMessageForUpdatingPatientsList==false) {
              alert.present();
              this.hasShowedMessageForUpdatingPatientsList = true;
          }
          setTimeout( () => {
              this.promptToSelectPatients();
              }, 1500);
      }
  }

  public promptToSelectTypeOfEvent() {
    let alert = this.alertController.create({
        title: "Art der Veranstaltung" // "Type of event"
    });
    for(let eventType of CalendarEvent.ALL_EVENT_TYPES) {
      alert.addInput({
          type: 'radio',
          label: CalendarEvent.getEventTypeByNumber(eventType),
          value: String(eventType),
          checked: this.event.typeOfMeeting == eventType
      });
    }
    alert.addButton('Abbrechen'); // Cancel
    alert.addButton({
      text: 'OK',
      handler: data => {
        console.log(this.event);
        console.log(data);
        this.event.typeOfMeeting = Number(data);
        this.eventTypeInText = CalendarEvent.getEventTypeByNumber(this.event.typeOfMeeting);
        console.log(this.event);
      }
    });
    alert.present();
  }


}



